import db from '@/lib/db';
import { NextResponse } from 'next/server';

export async function GET() {
    try {
        const products = db.prepare('SELECT * FROM products ORDER BY created_at DESC').all();
        return NextResponse.json(products);
    } catch (error) {
        return NextResponse.json({ error: 'Failed to fetch products' }, { status: 500 });
    }
}

export async function POST(request) {
    try {
        const body = await request.json();
        const name = body.name;
        const description = body.description || '';
        const quantity = parseInt(body.quantity) || 0;
        const user_id = parseInt(body.user_id);
        const image_url = body.image_url;

        if (!name || !user_id || !image_url) {
            return NextResponse.json({ error: 'Name, image_url, and user_id are required' }, { status: 400 });
        }

        // Check if user is admin
        const user = db.prepare('SELECT role FROM users WHERE id = ?').get(user_id);
        if (!user || user.role !== 'admin') {
            return NextResponse.json({ error: 'Only admins can create products' }, { status: 403 });
        }

        const result = db.prepare(
            'INSERT INTO products (name, description, quantity, image_url) VALUES (?, ?, ?, ?)'
        ).run(name, description, quantity || 0, image_url);

        return NextResponse.json({ id: result.lastInsertRowid, name, description, quantity: quantity || 0, image_url });
    } catch (error) {
        return NextResponse.json({ error: 'Failed to create product' }, { status: 500 });
    }
}
